// import jsonp from 'assets/js/jsonp'
import {
  // commonParams,
  // options,
  HOSTURL
} from './config'
import axios from 'axios'
export function getTopList(limit, offset) {
  // const url = 'https://c.y.qq.com/v8/fcg-bin/fcg_myqq_toplist.fcg'

  // const data = Object.assign({}, commonParams, {
  //   uin: 0,
  //   needNewCode: 1,
  //   platform: 'h5'
  // })

  // return jsonp(url, data, options)

  const url = '/top/mv';
  const data = {
    limit: limit,
    offset: offset
  }
  return axios.get(`${HOSTURL}${url}`, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data);
  })
}

export function getMusicList(topid) {
  // const url = 'https://c.y.qq.com/v8/fcg-bin/fcg_v8_toplist_cp.fcg'

  // const data = Object.assign({}, commonParams, {
  //   topid,
  //   needNewCode: 1,
  //   uin: 0,
  //   tpl: 3,
  //   page: 'detail',
  //   type: 'top',
  //   platform: 'h5'
  // })

  // return jsonp(url, data, options)

  const url = '/mv/url'
  const data = {
    id: topid
  }

  return axios.get(`${HOSTURL}${url}`, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data);
  })
}

export function getCommentDetail(topid) {
  const url = '/comment/mv';
  const data = {
    id: topid
  }

  return axios.get(`${HOSTURL}${url}`, {
    params: data
  }).then((res) => {
    return Promise.resolve(res.data);
  })
}

// 获取排行榜列表

export function getToplist() {
  const url = '/toplist';

  return axios.get(`${HOSTURL}${url}`).then(res => {
    return Promise.resolve(res.data);
  })
}

// 获取排行榜详情

export function getToplistDetail(id) {
  const url = '/top/list';
  const data = {
    idx: id
  }

  return axios.get(`${HOSTURL}${url}`, {
    params: data
  }).then(res => {
    return Promise.resolve(res.data);
  })
}



